import gRPCHost from 'gRPCHost';
import * as grpcWeb from 'grpc-web';

const { WechatUnificationServicePromiseClient } = require('../proto/wechat-unification-service_grpc_web_pb');
const {
    GetSessionIdRequest, GetConnectionStatusRequest, GetYxUserInfoRequest,
    ListUserDevicesRequest, ReconnectRequest, DisconnectRequest,
    GetJsApiSignatureRequest,
} = require('../proto/wechat-unification-service_pb');

class WechatUnificationService {
    constructor() {
        this.client = new WechatUnificationServicePromiseClient(gRPCHost, null, null);
    }

    GetSessionId(channelId, code) {
        const request = new GetSessionIdRequest();
        request.setChannelid(channelId);
        request.setCode(code);
        return this.client.getSessionId(request);
    }

    GetJsApiSignature(channelId, url) {
        const request = new GetJsApiSignatureRequest();
        request.setSessionid(channelId);
        request.setUrl(url);
        return this.client.getJsApiSignature(request);
    }

    GetConnectionStatus(sessionId) {
        const request = new GetConnectionStatusRequest();
        request.setSessionid(sessionId);
        return this.client.getConnectionStatus(request);
    }

    GetYxUserInfo(sessionId) {
        const request = new GetYxUserInfoRequest();
        request.setSessionid(sessionId);
        return this.client.getYxUserInfo(request);
    }

    ListUserDevices(sessionId) {
        const request = new ListUserDevicesRequest();
        request.setSessionid(sessionId);
        return this.client.listUserDevices(request);
    }

    Reconnect(sessionId, targetChannelId, deviceIdentifier, deviceDescription) {
        const request = new ReconnectRequest();
        request.setSessionid(sessionId);
        request.setTargetChannelid(targetChannelId);
        request.setDeviceidentifier(deviceIdentifier);
        request.setDevicedescription(deviceDescription);

        return this.client.reconnect(request);
    }

    Disconnect(sessionId, targetChannelId) {
        const request = new DisconnectRequest();
        request.setSessionid(sessionId);
        request.setTargetchannelid(targetChannelId);
        return this.client.disconnect(request);
    }

}

export default new WechatUnificationService();